main {
  .about-us {
    .about {
      position: relative;
      h2 {
        position: absolute;
        top: -150px;
        font-size: 196px;
        color: rgba(0, 0, 0, 0.031);
        font-weight: bold;
        letter-spacing: 20px;
        width: 100%;
        text-align: center;
      }
      .background-cut {
        background:linear-gradient(rgba(127,127,127,0), rgba(127,127,127,0.3)), url('../../assets/img/aboutBackground.png') no-repeat center center;
        background-size: cover;
        min-height: 702px;
        clip-path: polygon(50% 0%, 100% 0, 100% 100%, 46% 100%, 46% 46%, 0 46%, 0 1%);
      }

      .about-content {
        display: flex;
        justify-content: center;
        position: relative;
        top: -100px;
        .about-text {
          margin-right: 200px;
          width: 18%;
          position: relative;
          top: -192px;
          height: 400px;
          h4 {
            font-size: 1.125rem;
            text-align: center;
            font-weight: bold;
            position: relative;
            margin-bottom: 58px;

            &::before {
              content: '';
              width: 13px;
              height: 13px;
              position: absolute;
              top: 40px;
              left: 50%;
              transform: translateX(-50%);
              background: url('../../assets/img/x-icon.png') no-repeat center center;
              background-size: 100% 100%;
            }
          }

          p {
            font-size: 1.375rem;
            font-family: 'Playfair Display';
            font-style: italic;
            line-height: 1.727;
            margin-bottom: 34px;
          }

          a {
            color: $mainColor;
            text-decoration: none;
            border-bottom: 2px solid $mainColor;
            padding-bottom: 1px;
            font-weight: 600;
          }
        }

        .video {
          width: 26%;
          position: relative;
          height: fit-content;
          z-index: 1;

          &:active {
            .control {
              .play-container {
                transform: translate(-50%,-50%) scale(.9);
              }
            }
          }

          video {
            width: 100%;
            height: 100%;
            object-fit: cover;
            box-shadow: 20px 34.641px 50px 0px rgba(0, 0, 0, 0.2);
          }

          .control {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            pointer-events: none;
            .play-container {
              border-style: solid;
              border-width: 1px;
              border-color: rgb(255, 255, 255);
              border-radius: 50%;
              width: 144px;
              height: 144px;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%,-50%) scale(1);
              z-index: 2;
              transition: opacity .3s ease-in-out, transform .3s ease-in-out;

              svg {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%,-50%);
              }
            }
          }
        }
      }
    }

    .team {
      position: relative;
      top: -70px;
      min-height: 320px;
      margin-bottom: 180px;
      display: flex;
      justify-content: center;
      align-items: center;
      .arrows {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        margin-top: 140px;
        padding-bottom: 50px;
        position: absolute;
        display: flex;

        .arrow {
          position: absolute;
          border-style: solid;
          border-width: 1px;
          border-color: rgb(230, 230, 230);
          border-radius: 50%;
          background-color: rgb(255, 255, 255);
          box-shadow: 0px 0px 25px 0px rgba(0, 0, 0, 0.16);
          width: 49px;
          height: 48px;
          top: 50%;
          transform: translateY(-50%);
          display: flex;
          justify-content: center;
          align-items: center;
          transition: transform .3s ease-in-out;

          &:active {
            transform: translateY(-50%) scale(.90);
          }

          div {
            width: 16px;
            height: 14px;
            background: url('../../assets/img/team-arrow.png') no-repeat center center;
            background-size: cover;
            transition: transform .3s ease-in-out;
          }

          .arrow-left {
            transform: rotate(180deg);
          }
        }

        .arrow:nth-child(1){
          left: 10%;
        }
        .arrow:nth-child(2){
          right: 10%;
        }
      }
      .absolute {
        width: 100%;
        height: 100%;
        position: absolute;
        h2 {
          position: absolute;
          top: 0px;
          font-size: 196px;
          color: rgba(0, 0, 0, 0.031);
          font-weight: bold;
          letter-spacing: 20px;
          width: 100%;
          text-align: center;
          pointer-events: none;
        }
      }

      .slider-team {
        margin-top: 140px;
        padding-bottom: 50px;
        width: 60%;
        position: absolute;
        display: flex;
        justify-content: center;
        overflow: hidden;

        .slider {
          display: flex;
          justify-content: center;
          width: 4000px;
          position: relative;
          left: 470px;
          padding-bottom: 30px;
          transition: all .3s ease-in;
        }

        .member:nth-child(odd) {
          top: 30px;
          z-index: 2;
        }

        .member:nth-child(even) {
          z-index: 1;
        }

        .member:nth-child(5) {
          margin-left: 400px;
        }

        .member {
          position: relative;
          box-shadow: 10px 17.321px 50px 0px rgba(0, 0, 0, 0.2);
          background: #fff;
          width: 260px;
          height: 278px;
          border-radius: 4px;
          background: #fff;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          text-align: center;
          margin-right: -10px;
          margin-left: -10px;
          transition: all .3s ease-in;
          cursor: pointer;

          &:hover {
            background-color: $mainColor;

            h4 {
              color: #fff;
            }

            span:nth-child(2){
              &::before {
                background-image: url('../../assets/img/waves-active.png');
              }
            }

            span {
              color: rgb(179, 231, 255);
            }
          }

          .top-member img {
            margin-bottom: 16px;
          }

          h4 {
            font-size: 1.125rem;
            color: $mainColor;
            font-weight: bold;
            margin-bottom: 3px;
            transition: all .3s ease-in;
          }

          span:nth-child(2){
            margin-bottom: 36px;
            position: relative;

            &::before {
              content: '';
              width: 31px;
              height: 6px;
              background: url('../../assets/img/waves.png') no-repeat center center;
              background-size: cover;
              position: absolute;
              top: 30px;
              left: 50%;
              transform: translateX(-50%);
            }
          }

          span{
            transition: all .3s ease-in;
            display: block;
            font-family: "Raleway";
            color: rgb(140, 140, 140);
            font-size: 0.875rem;
          }
        }


      }
    }
  }

  .updates {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding-top: 37px;
    margin-bottom: 120px;

    h4 {
      text-align: center;
      font-size: 1.125rem;
      font-weight: bold;
      letter-spacing: 2px;
      margin-bottom: 73px;
      position: relative;

      &::before {
        content: '';
        position: absolute;
        top: 40px;
        left: 50%;
        transform: translateX(-50%);
        width: 13px;
        height: 13px;
        background: url('../../assets/img/x-icon.png') no-repeat center center;
        background-size: 100% 100%;
      }
    }

    .updates-container {
      display: flex;
      justify-content: center;
      align-items: center;
        .update {
          width: 14%;
          box-shadow: 0px 0px 0px 0px rgba(0, 0, 0, 0.2);
          transition: box-shadow .3s ease-in-out;
          cursor: pointer;
        img {
          width: 100%;
        }

        p {
          padding: 27px 25px 26px;
          font-family: "Raleway";
          color: rgb(120, 120, 120);
          line-height: 1.5;
          transition: color .3s ease-in-out;
        }

        &:hover {
          box-shadow: 0px 2px 30px 0px rgba(0, 0, 0, 0.2);

          p {
            color: rgb(0, 34, 255);
          }
        }
      }

      .update:nth-child(1), .update:nth-child(2) {
        margin-right: 30px;
      }

    }

    button {
      margin-top: 40px;
      margin-right: auto;
      margin-left: auto;
      width: 197px;
      height: 44px;
    }
  }
}
